<template>
  <div>
    <div class="content" style="font-size:16px;">
      <mu-row>
        <mu-col span="4">
          <div>事件编号</div>
        </mu-col>
        <mu-col span="8">
          <div>{{this.list.eventId}}</div>
        </mu-col>
        <mu-col span="4">
          <div>发送人</div>
        </mu-col>
        <mu-col span="8">
          <div>{{this.list.sendName}}</div>
        </mu-col>
        <mu-col span="4">
          <div>事件类型</div>
        </mu-col>
        <mu-col span="8">
          <div>{{this.list.eventType}}</div>
        </mu-col>
      </mu-row>
      <mu-row>
        <mu-col span="4">
          <div>事件类型</div>
        </mu-col>
        <mu-col span="8">
          <div>{{this.list.eventType}}</div>
        </mu-col>
        <mu-col span="4">
          <div>上传时间</div>
        </mu-col>
        <mu-col span="8">
          <div>{{this.list.createTime}}</div>
        </mu-col>
      </mu-row>
      <div style="font-size:16px">现场图片</div>
      <div class="handleFlex">
        <div v-for="(item,index) in list2" :key="index" @click="clickImagTO(index)">
          <div class="carouselImg1">
            <img :style="imgWidth" :src="url+item" alt srcset>
          </div>
        </div>
      </div>
      <div class="describe">
        <div style="font-size:16px">详细描述</div>
        <textarea name="textarea" id="textarea" cols rows disabled></textarea>
      </div>
    </div>
  </div>
</template>

<script>
var token = sessionStorage.getItem("token");
export default {
  data() {
    return {
      imgWidth: "width:280px;height:auto;",
      url: "http://47.112.16.168:8888/",
      token: sessionStorage.getItem("token"),
      name: "",
      msg: "hello 数据详情",
      list: [],
      list2: [],
      startTime: "",
      endTime: "",
      eventType: "",
      street: "",
      // token: "",
      eventId: ""
    };
  },
  created() {
    // this.token = this.$store.state.token;
    this.name = this.$route.query.name;
    this.eventId = this.$route.query.eventId;
    console.log("en", this.eventId);
  },
  methods: {
    clickImagTO(index) {
      this.imgWidth = "width:300px;height:auto;";
    },
    sendEvent() {
      if (this.name == "发送列表") {
        this.$router.replace("/SendList");
      } else {
        this.$router.replace("/EventList");
      }
    },
    async http() {
      console.log("en111", this.eventId);
      var url = "http://47.112.16.168:8800/event/detail?id=" + this.eventId;
      var _this = this;
      await this.$http({
        method: "get",
        url: url,
        headers: {
          token: _this.token
        }
        // data:{
        //    id: this.eventId
        // }
      })
        .then(res => {
          console.log("123", res);

          this.list = res.data.data;
          console.log("111111", this.list);
          $("#textarea").val(this.list.eventDetails);
          // debugger
          _this.list2 = res.data.data.photo1.split(",");
          // debugger
        })
        .catch(function() {
          console.log("出错了");
        });
    }
  },
  async mounted() {
    await this.http();
  }
};
</script>
<style scoped>
.content {
  /* width: 100%; */
  margin: 10px;
  padding: 10px;
  border: 1px #ccc solid;
  box-shadow: 2px 1px 2px #888888;
  height: 100%;
  background: white;
}
.carouselImg1 img {
  width: 20%;
}
.carouselImg1 {
  display: flex;
  flex-direction: row;
  width: 98vw;
  padding: 0 10px;
  margin: 20px 0;
  /* border-bottom: #ccc 4px solid; */
}
.carouselImg1 div {
  width: 23%;
}
.carouselImg1 img {
  width: 99%;
}
.describe {
  margin: 20px 0;
}
#textarea {
  width: 100%;
  height: 100px;
  border: none;
}
.handleFlex{
  display: flex;
  flex-direction: row;
  flex-flow:row wrap;

}
.handleFlex div{
  width: 100%;
}
</style>



